.header{
    display: flex;
    justify-content: space-between;
    margin: 10px;
}

.nav-items > ul {
    list-style: none;
    display: flex;
}

.nav-items > ul > li {
    padding: 10px;
}

.logo{
   width: 80px;
}

.card{
    width: 200px;
    border: 1px solid black;
    margin: 10px;
    padding: 10px;
   
}

.card > img {
    width: 100%;
}

.card >h2 {
    font-size: 30px;
    margin: 0;
}
.card >h3 {
    font-size: 20px;
    margin: 0;
}
.card >h4 {
    font-size: 15px;
    margin: 0;
    padding: 3px;
}

.restaurant-list{
    display: flex;
    flex-wrap: wrap;
}
.shimmerCard{
    width: 200px;
    height: 200px;
    color: rgb(141, 90, 90);
    margin: 20px;
}
.shimmer-card{
    height: 200px;
    width: 200px;
    background-color: lightgray;
    margin: 20px;
}